





























     declare global {
        interface Window{ $clamp: any }
    }	

	import { Component, Vue } from "vue-property-decorator";

    @Component({props:{post:{type: Object, require: true }}})
	export default class Devotional extends Vue {
        
        mounted() {
            var ellipsis = document.getElementById(this.$props.post.id);
            window.$clamp(ellipsis, {clamp: 3, useNativeClamp: false, truncationHTML: "&ensp;"});
        }
		 _formatDate(date: string) {
			const months = [
				"JANUARY", "FEBRUARY", "MARCH","APRIL","MAY", "JUN", "JULY", "AUGUST", "SEPTEMBER", "OCTOBER", "NOVEMBER", "DECEMBER"
				];
			var _date = new Date(date);
			return months[_date.getMonth()] + " " + _date.getDate()  + " " + _date.getFullYear();
		}
}
